<template>
  <div>
    <div class="flex mb-20">
      <div class="">
        时间: <DatePicker v-model="timeZone" type="daterange" :editable="false" placement="bottom-start"
          placeholder="请选择时间" style="width: 240px;margin-left:10px;" @on-change="timeChange">
        </DatePicker>
      </div>
    </div>

    <Table :columns="columns" :data="data" border></Table>
    <div class="pageBox">
      <span>
        <span class="mr-10">共{{pageTotal}}条</span>
        每页{{pageSize}}条
      </span>
      <Page style="margin-right:120px" :total="pageTotal" :pageSize="pageSize" @on-change="pageChange"></Page>
      <span></span>
    </div>
  </div>
</template>
<script>
import { dateFormate } from '@/utils/dateFormate.js'
import { number_formate } from '@/utils/number_formate.js'
export default {
  data () {
    return {
      columns: [
        {
          title: 'ID',
          key: 'id',
          width: 260,
        },
        {
          title: '账变',
          key: 'amount',
          width: 220,
          render: (h, params) => {
            return h('span', {}, number_formate(params.row.amount))
          },
        },
        {
          title: '时间',
          width: 180,
          render: (h, params) => {
            return h('div', {}, dateFormate(new Date(params.row.create_time * 1000), 'yyyy-MM-dd hh:mm:ss'))
          },
        },
        {
          title: '描述',
          render: (h, params) => {
            return h('div', {}, params.row.desc.split('|')[0])
          },
        },
      ],
      data: [

      ],
      timeZone: '',
      page: 1,
      pageSize: 10,
      pageTotal: 0
    }
  },
  mounted () {
    this.get_econo_change()
  },
  methods: {
    timeChange () {
      let endTime = this.timeZone[1]
      endTime.setHours(23)
      endTime.setMinutes(59)
      endTime.setSeconds(59)
      this.timeZone[1] = endTime
      this.page = 1
      this.get_econo_change()
    },
    pageChange (page) {
      this.page = page
      this.get_econo_change()
    },
    get_econo_change () {
      let params = {
        pos: (this.page - 1) * this.pageSize,
        offset: this.pageSize,
      }
      if (this.timeZone[0]) {
        params.beg_create_time = this.timeZone[0].getTime() / 1000
        params.end_create_time = this.timeZone[1].getTime() / 1000
      }
      this.$post('cgi/get_econo_change', params).then(resData => {
        if (resData.ret_code == 0) {
          this.pageTotal = resData.ret_data.total_num
          this.data = resData.ret_data.data_list
        }
      })
    }
  }
}
</script>